body {
  font-family: "Roboto", sans-serif;
  font-weight: 300; }

.title {
  font-size: 34px;
  line-height: 100%;
  color: #06102F;
  margin-top: 0;
  font-weight: 400; }

.arrow-link {
  font-size: 10px;
  line-height: 100%;
  color: #48474E;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  font-weight: 700; }
  .arrow-link:hover:after {
    right: -25px; }
  .arrow-link:hover:before {
    width: 25px; }
  .arrow-link:after {
    top: 50%;
    right: -15px;
    position: absolute;
    content: '';
    width: 6px;
    height: 6px;
    border-top: 2px solid #292B2C;
    border-right: 2px solid #292B2C;
    transform: translateY(-50%) rotate(45deg);
    transition-duration: 0.3s; }
  .arrow-link:before {
    top: 0;
    left: 100%;
    position: absolute;
    content: '';
    width: 18px;
    height: 100%;
    transition-duration: 0.3s; }

.tab {
  display: flex;
  align-items: center;
  height: 860px;
  margin-bottom: 105px; }
  .tab__img-box {
    position: relative;
    width: 50%;
    height: 100%; }
    @media (max-width: 767px) {
      .tab__img-box {
        height: 500px;
        width: 100%; } }
    @media (max-width: 449px) {
      .tab__img-box {
        height: 250px; } }
  .tab__img-item {
    top: 0;
    left: -10%;
    position: absolute;
    width: 100%;
    height: 100%;
    transition-duration: 0.5s;
    opacity: 0; }
    .tab__img-item--elegance {
      background: url(../img/elegance-img.jpg) center; }
    .tab__img-item--creation {
      background: url(../img/creation-img.jpg) center; }
    .tab__img-item--family {
      background: url(../img/family-office-img.jpg) center; }
    .tab__img-item--expirience {
      background: url(../img/expirience-img.jpg) center; }
    @media (max-width: 767px) {
      .tab__img-item {
        background-size: cover; } }
  .tab__info {
    position: relative;
    width: 50%;
    height: 760px;
    background-color: #F8F9FA; }
    @media (max-width: 1023px) {
      .tab__info {
        height: 600px; } }
    @media (max-width: 767px) {
      .tab__info {
        width: 100%;
        height: 450px; } }
    @media (max-width: 449px) {
      .tab__info {
        height: 550px; } }
  .tab__content {
    top: 50%;
    left: 50%;
    position: absolute;
    width: 445px;
    transform: translate(-50%, -60%);
    transition-duration: 0.5s;
    opacity: 0; }
    .tab__content-title {
      margin-bottom: 40px; }
      @media (max-width: 1023px) {
        .tab__content-title {
          margin-bottom: 30px; } }
    .tab__content-desc {
      font-size: 16px;
      line-height: 32px;
      color: #06102F;
      margin-bottom: 30px;
      text-align: justify; }
      @media (max-width: 1023px) {
        .tab__content-desc {
          margin-bottom: 20px;
          line-height: 28px; } }
    @media (max-width: 1023px) {
      .tab__content {
        width: 100%;
        padding: 0 20px; } }
  .tab__link {
    display: inline-block;
    margin-top: 20px; }
    @media (max-width: 1023px) {
      .tab__link {
        margin-top: 0; } }
  .tab__nav {
    top: 100px;
    left: 50%;
    position: absolute;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    padding: 6px;
    border-radius: 2px;
    background-color: #fff;
    transform: translateX(-50%); }
    .tab__nav-radio {
      display: none; }
    .tab__nav-item {
      font-size: 10px;
      line-height: 48px;
      color: #869099;
      display: block;
      height: 100%;
      margin-right: 5px;
      padding: 0 20px;
      border-radius: 2px;
      font-weight: 400;
      text-transform: uppercase;
      letter-spacing: 0.25em;
      white-space: nowrap;
      cursor: pointer;
      transition-duration: 0.3s; }
      .tab__nav-item:last-child {
        margin-right: 0; }
      .tab__nav-item:hover {
        background-color: rgba(6, 16, 47, 0.05); }
      @media (max-width: 767px) {
        .tab__nav-item {
          padding: 0 10px;
          line-height: 36px; } }
      @media (max-width: 449px) {
        .tab__nav-item {
          padding: 0 5px;
          line-height: 24px;
          letter-spacing: 0.05em; } }
    .tab__nav-radio:checked + .tab__nav-item {
