        top: 10px;
        padding: 2px; } }
  @media (max-width: 1023px) {
    .tab {
      height: 650px; } }
  @media (max-width: 767px) {
    .tab {
      flex-direction: column;
      height: auto; } }

.show-content {
  transition-delay: 0.5s;
  transform: translate(-50%, -50%);
  opacity: 1; }

.hide-content {
  transform: translate(-50%, -60%);
  opacity: 0; }

.show-picture {
  transition-delay: 0.5s;
  left: 0;
  opacity: 1; }

.hide-picture {
  left: -10%;
  opacity: 0; }

